import React from 'react'
import './App.css';
import Key from './Key'
import Letter from './Letter'
import Counter from './Counter'

const azerty = "azertyuiopqsdfghjklmwxcvbn"
const words = [
    "Natachat",
    "Jade",
    "Thomas",
    "Rari",
    "Rarette",
    "Troubadour",
    "Chaton"
]

class App extends React.Component {
    constructor() {
        super()

        this.getFeedback = this
            .getFeedback
            .bind(this)

        this.newGame = this
            .newGame
            .bind(this)

        this.testLetter = this
            .testLetter
            .bind(this)


        this.state = {
            keyboard: this.getKeyboard(),
            word: this.getWord(),
            response: [],
            counter: 8,
            status: 'Jeu du pendu',
            bgColor: '#111111',
            display: 'flex',
            btnText: 'Commencer'
        }
    }

    getKeyboard() {
        return azerty
            .toUpperCase()
            .split('')
    }

    getWord() {
        const index = Math.floor(Math.random() * words.length)
        return words[index]
            .toUpperCase()
            .split('')
    }

    testLetter(e) {
        const {response, word} = this.state;
        const letterToTest = e.currentTarget.value || e
            .key
            .toUpperCase();

        this.setState({
            response: [
                ...response,
                letterToTest
            ]
        })

        word.includes(letterToTest)
            ? this.setState((previousState) => ({counter: previousState.counter}), this.getStatus)
            : this.setState((previousState) => ({
                counter: previousState.counter - 1
            }), this.getStatus)
    }

    newGame() {
        this.setState({
            word: this.getWord(),
            response: [],
            counter: 8,
            status: 'en cours',
            display: 'none'
        })
    }

    getFeedback(letter) {
        const {response, word} = this.state;
        const goodResponse = word.filter(elt => response.includes(elt))
        if (goodResponse.includes(letter)) {
            return false
        }
        return true
    }

    getStatus() {
        const {response, word, counter} = this.state;
        const goodResponse = word.filter(elt => response.includes(elt))

        counter > 0
        ? goodResponse.join('') === word.join('')
            ? this.setState({status: 'You win', bgColor: 'green', display: 'flex'})
            : this.setState({status: 'en cours', bgColor: 'grey'})
        : this.setState({status: 'You lose', bgColor: 'red', display: 'flex'})
        
    

    }

    componentDidUpdate() {
        this.state.display === 'none' ? window.addEventListener('keypress', this.testLetter) : window.removeEventListener('keypress', this.testLetter)
    }

    render() {

        const {
            word,
            counter,
            status,
            bgColor,
            display,
            btnText
        } = this.state

        const help = <p>The word was {word}</p>

        return (
            <div className="App">
                <div
                    className="intro"
                    style={{
                    backgroundColor: `${bgColor}`,
                    display: `${display}`
                }}>
                    <h1>{status}</h1>
                    {help}
                    <button className="reset" onClick={this.newGame}>{btnText}</button>
                </div>
                <div className="statebox">
                    <Counter counter={counter} status={status}/>
                    <button className="reset" onClick={this.newGame}>Nouvelle Partie</button>
                </div>

                <div className='word'>
                    {word.map((letter, i) => <Letter key={i} letter={letter} feedback={this.getFeedback(letter)}/>)}
                </div>
                <div className='keyboard'>
                    {this
                        .state
                        .keyboard
                        .map((letter, i) => <Key
                            key={i}
                            value={letter}
                            letter={letter}
                            feedback={this.getFeedback(letter)}
                            onClick={this.testLetter}/>)}
                </div>
            </div>
        )

    }

}

export default App;
