import React from 'react';

function Key({letter, value, feedback, onClick, onTouchStart}) {
    return (
        <button
            className={`keyboard-letter ${feedback
            ? 'key'
            : 'key-right'}`}
            value={value}
            onClick={onClick}
            onTouchStart={onTouchStart}>
            <p className='text'>{letter}</p>
        </button>
    )

}

export default Key